.content-body {
    min-height: 100vh;
    padding: 54px 15px 60px;
    &.ppMain-content {
        padding: 0 0 70px
    }
    &.fixed-scroll {
        height: 100vh;
        overflow: hidden;
    }
    >.fixed-footer {
        position: fixed;
        z-index: 10;
        width: 100%;
        left: 0;
        bottom: 0;
        @include flexCenter;
        &.full-btn {
            button {
                width: 100%;
            }
        }
        button {
            width: 50%;
            height: 60px;
            background: $mainColor;
            color: #fff;
            @include fontSize(15px,400);
            border: 0;
            span {
                color: #fff;
                @include fontSize(15px,400);
            }
            &.cancel {
                background: #000;
            }
        }
    }
    .subEtc-container {
        margin: 10px 0 20px;
        p {
            @include fontSize(12px,400);
            color: #666;
            line-height: 18px;
        }
    }
    .tab-section {
        >ul {
            @include flexCenter;
            border-bottom: 1px solid #eee;
            &.tab-list2 {
                li {
                    width: 50%;
                }
            }
            &.small-list {
                li {
                    @include fontSize(12px,500);
                    width: 25%;
                    height: 44px;
                    &.active {
                        @include fontSize(12px,700);
                        color: #000;
                        translate: .3s;
                        &:after {
                            content: "";
                            width: 100%;
                            position: absolute;
                            bottom: -1px;
                            height: 1px;
                            background: #000;
                            left: 50%;
                            transform: translate(-50%,0);
                            animation: tabLine .3s;
                        }
                        span {
                            @include fontSize(12px,700);
                            color: #000;
                        }
                    }
                    span {
                        margin-left: 4px;
                        @include fontSize(12px,500);
                        color: #999;
                    }
                }
            }
            >li {
                text-align: center;
                height: 42px;
                width: calc( 100%/3 );
                @include flexAllCenter;
                @include fontSize(15px,400);
                color: #999;
                position: relative;
                @keyframes tabLine {
                    0% {
                        width: 0;
                    }
                    100% {
                        width: 100%;
                    }
                }
                &:before {
                    content: "";
                    position: absolute;
                    right: 0;
                    background: #edf0f3;
                    height: 9px;
                    width: 1px;
                    top: 50%;
                    transform: translate(0,-50%);
                }
                &.active {
                    @include fontSize(15px,700);
                    color: #000;
                    translate: .3s;
                    &:after {
                        content: "";
                        width: 100%;
                        position: absolute;
                        bottom: -1px;
                        height: 3px;
                        background: #000;
                        left: 50%;
                        transform: translate(-50%,0);
                        animation: tabLine .3s;
                    }
                }
                &:last-child {
                    &:before {
                        display: none;
                    }
                }
            }
        }
        .target-wrap {
            .tabTarget {
                display: none;
                &.active {
                    display: block;
                }
            }
        }
    }
    &.listUp-content {
        padding-bottom: 0;
        .listUp-container {
            padding-top: 10px;
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 64px );
            }
            ul {
                li {
                    @include flexCenter;
                    padding: 20px 0;
                    border-bottom: 1px solid #eee;
                    &:first-child {
                        border-top: 1px solid #eee;
                    }
                    .listUp-warpper {
                        width: calc( 100% - 28px );
                        margin-right: 20px;
                        .listUp-category {
                            color: #333;
                            @include fontSize(11px,400);
                            line-height: 17px;
                            margin-bottom: 5px;
                            span {
                                color: #333;
                                @include fontSize(11px,400);
                                line-height: 17px;
                            }
                        }
                        .listUp-text {
                            @include fontSize(15px,500);
                            line-height: 22px;
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 2;
                            overflow: hidden;
                        }
                        .listUp-date {
                            display: block;
                            margin-top: 5px;
                            @include fontSize(12px,400);
                            line-height: 14px;
                            color: #999;
                        }
                    }
                    >img {
                        width: 8px;
                    }
                }
            }
        }
    }
}

//컨텐츠 간격 바
.barSection {
    height: 7px;
    background: #f6f6f6;
    border-top: 1px solid #dcdcdc;
    border-bottom: 1px solid #dcdcdc;
}

header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 54px;
    z-index: 10;
    .transparent-header {
        padding: 0 15px;
        height: 54px;
        @include flexCenter;
        position: relative;
        @keyframes showing {
            0% {
                opacity: 0;
            }
            100% {
                opacity: 1;
            }
        }
        @keyframes hideing {
            0% {
                opacity: 1;
            }
            100% {
                opacity: 0;
            }
        }
        &.active {
            background: #fff;
            transition: .3s;
            img {
                display: none;
                animation: hideing .3s ease-in;
                &.black-img {
                    display: block;
                    animation: showing .3s ease-in;
                }
            }
        }
        &.passive {
            img {
                display: block;
                animation: showing .3s ease-in;
                &.black-img {
                    display: none;
                    animation: hideing .3s ease-in;
                }
            }
        }
        >img {
            width: 150px;
        }
        img {
            &.black-img {
                display: none;
            }
        }
        button {
            @include btnNone;
        }
        .list-btn {
            position: relative;
            margin-right: 16px;
            img {
                width: 26px;
            }
            span {
                display: inline-block;
                position: absolute;
                top: -5px;
                right: -7px;
                @include fontSize(10px,700);
                color: #fff;
                background: $mainColor;
                padding: 0px 6px;
                border-radius: 99px;
            }
        }
        .btns-wrap {
            position: absolute;
            right: 15px;
            button {
                margin-right: 10px;
                &:last-of-type {
                    margin-right: 0;
                }
                img {
                    width: 19px;
                }
            }
        }
    }
    .default-header {
        padding: 0 15px;
        height: 54px;
        @include flexCenter;
        position: relative;
        background: #fff;
        .back-btn {
            @include btnNone;
            margin-right: 5px;
            padding-bottom: 2px;
            img {
                width: 14px;
            }
        }
        .headerTitle {
            @include fontSize(20px,500);
            line-height: 29px;
        }
        .btns-wrap {
            position: absolute;
            right: 15px;
            button {
                @include btnNone;
                margin-right: 10px;
                &:last-of-type {
                    margin-right: 0;
                }
                img {
                    width: 19px;
                }
            }
        }
    }
}


footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 10;
    .tab-footer {
        .shadow-box {
            height: 15px;
            opacity: 0.15;
            background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #000000);
        }
        ul {
            width: 100%;
            @include flexCenter;
            li {
                width: 50%;
                text-align: center;
                padding: 10px 0 15px;
                background: #fff;
                img {
                    margin-bottom: 5px;
                    width: 25px;
                }
                p {
                    line-height: 15px;
                    @include fontSize(10px,300);
                    color: #a2a2a2;
                }
                &.active {
                    p {
                        color: $mainColor;
                        @include fontSize(10px,500);
                    }
                }
            }
        }
    }
}


input[type=checkbox]:disabled + label span {
    background: #c9c9c9 !important;
}